<template>
    <div class="table">
        <div class="topb">
            <h2></h2>
        </div>
        <div class="container">
            <div class="handle-box">

                <el-input v-model="select_word" size="mini" placeholder="" class="handle-input"></el-input>
                <el-button type="primary" size="mini" @click="centerDialogVisible = true" class="button1"></el-button>
            </div>
        </div>
        <el-table size="mini" ref="multipleTable" border style="width:100%" height="680px" :data="data" @selection-change="handleSelectionChange">

            <el-table-column label="" width="110" align="center">
                <template slot-scope="scope">
                    <div class="songList-img">
                        <img :src="getUrl(scope.row.pic)" style="width:100%"/>
                    </div>
                    <el-upload :action="uploadUrl(scope.row.id)" :before-upload="beforeAvatorUpload"
                        :on-success="handleAvatorSuccess">
                        <el-button size="mini"></el-button>
                    </el-upload>
                </template>
            </el-table-column>
            <el-table-column prop="title" label="" width="120" align="center"></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <p style="height:100px;overflow:scroll">{{scope.row.introduction}}</p>
                </template>
            </el-table-column>
            <el-table-column prop="style" label="" width="120" align="center"></el-table-column>
            <el-table-column label="" width="110" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="songEdit(scope.row.id)"></el-button>
                </template>
            </el-table-column>
            <el-table-column label="" width="80" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="getComment(data[scope.$index].id)"></el-button>
                </template>
            </el-table-column>
            <el-table-column label="" width="150" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="handleEdit(scope.row)"></el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                background
                layout = "total,prev,pager,next"
                :current-page="currentPage"
                :page-size="pageSize"
                :total="tableData.length"
                @current-change="handleCurrentChange"
                >
            </el-pagination>
        </div>

        <el-dialog title="" :visible.sync="centerDialogVisible" width="400px" center>
            <el-form :model="registerForm" ref="registerForm" label-width="80px">
                <el-form-item prop="title" label="" size="mini">
                    <el-input v-model="registerForm.title" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="introduction" label="" size="mini">
                    <el-input v-model="registerForm.introduction" placeholder="" type="textarea"></el-input>
                </el-form-item>
                <el-form-item prop="style" label="" size="mini">
                    <el-input v-model="registerForm.style" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer">
                <el-button size="mini" @click="centerDialogVisible = false"></el-button>
                <el-button size="mini" @click="addSongList"></el-button>
            </span>
        </el-dialog>

        <el-dialog title="" :visible.sync="editVisible" width="400px" center>
            <el-form :model="form" ref="form" label-width="80px">
                <el-form-item prop="title" label="" size="mini">
                    <el-input v-model="form.title" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="introduction" label="" size="mini">
                    <el-input v-model="form.introduction" placeholder="" type="textarea"></el-input>
                </el-form-item>
                <el-form-item prop="style" label="" size="mini">
                    <el-input v-model="form.style" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer">
                <el-button size="mini" @click="editVisible = false"></el-button>
                <el-button size="mini" @click="editSave"></el-button>
            </span>
        </el-dialog>

        <el-dialog title="" :visible.sync="delVisible" width="400px" center>
            <div align="center"></div>
            <span slot="footer">
                <el-button size="mini" @click="delVisible = false"></el-button>
                <el-button size="mini" @click="deleteRow"></el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import {getAllSongList,setSongList,updateSongList,delSongList,getAllSongList2} from '../api/index';
import { mixin } from '../mixins/index';
import {mapGetters} from 'vuex';
export default {
    mixins: [mixin],
    data(){
        return{
            centerDialogVisible: false, //
            editVisible: false,         //
            delVisible: false,          //
            registerForm:{      //
                title: '',
                introduction: '',
                style: ''
            },
            form:{      //
                id: '',
                title: '',
                introduction: '',
                style: ''
            },
            tableData: [],
            tempData: [],
            select_word: '',
            pageSize: 5,    //
            currentPage: 1,  //
            idx: -1,          //
            multipleSelection: []   //
        }
    },
    computed:{
        //
        data(){
            return this.tableData.slice((this.currentPage - 1) * this.pageSize,this.currentPage * this.pageSize)
        },
        ...mapGetters([
            'listOfSongs',      //
            'userId',           //id
        ])
    },
    watch:{
        //table
        select_word: function(){
            if(this.select_word == ''){
                this.tableData = this.tempData;
            }else{
                this.tableData = [];
                for(let item of this.tempData){
                    if(item.title.includes(this.select_word)){
                        this.tableData.push(item);
                    }
                }
            }
        }
    },
    created(){
        this.getData();
    },
    methods:{
        //
        handleCurrentChange(val){
            this.currentPage = val;
        },
        //

        getData(){
            this.tempData = [];
            this.tableData = [];
            getAllSongList2(this.userId).then(res => {
                this.tempData = res;
                this.tableData = res;
                this.currentPage = 1;
            })
        },
        //
        addSongList(){
            let params = new URLSearchParams();
            params.append('title',this.registerForm.title);
            params.append('pic','/img/songListPic/123.jpg');
            params.append('introduction',this.registerForm.introduction);
            params.append('style',this.registerForm.style);
             params.append('userId',this.userId);

            setSongList(params)
            .then(res => {
                if(res.code == 1){
                    this.getData();
                    this.notify("","success");
                }else{
                    this.notify("","error");
                }
            })
            .catch(err => {
                console.log(err);
            });
            this.centerDialogVisible = false;
        },
        //
        handleEdit(row){
            this.editVisible = true;
            this.form = {
                id: row.id,
                title: row.title,
                introduction: row.introduction,
                style: row.style
            }
        },
        //
        editSave(){
            let params = new URLSearchParams();
            params.append('id',this.form.id);
            params.append('title',this.form.title);
            params.append('introduction',this.form.introduction);
            params.append('style',this.form.style);

            updateSongList(params)
            .then(res => {
                if(res.code == 1){
                    this.getData();
                    this.notify("","success");
                }else{
                    this.notify("","error");
                }
            })
            .catch(err => {
                console.log(err);
            });
            this.editVisible = false;
        },
        //
        uploadUrl(id){
            return `http://43.153.63.130:8888/songList/updateSongListPic?id=${id}`
        },
        //
        deleteRow(){
            delSongList(this.idx)
            .then(res => {
                if(res){
                    this.getData();
                    this.notify("","success");
                }else{
                    this.notify("","error");
                }
            })
            .catch(err => {
                console.log(err);
            });
            this.delVisible = false;
        },
        //
        songEdit(id,name){
            this.$router.push({path:`/ListSong`,query:{id}});
        },
        //
        getComment(id){
            this.$router.push({path: '/comment',query:{id}})
        },
        delAll(){
            for(let item of this.multipleSelection){
                this.handleDelete(item);
                this.deleteRow();
            }
            this.multipleSelection = [];
        },
         getUrl(url){
            return `http://43.153.63.130:8888/${url}`
        },
        beforeAvatorUpload(file){
            const isJPG = (file.type === 'image/jpeg')||(file.type === 'image/png');
            if(!isJPG){
                this.$message.error('jpgpng');
                return false;
            }
            const isLt2M = (file.size / 1024 /1024) < 2;
            if(!isLt2M){
                this.$message.error('2MB');
                return false;
            }
            return true;
        },
        //
        handleAvatorSuccess(res){
            let _this = this;
            if(res.code == 1){
                _this.getData();
                _this.$notify({
                    title: '',
                    type: 'success'
                });
            }else{
                _this.$notify({
                    title: '',
                    type: 'error'
                });
            }
        },
    }
}
</script>

<style scoped>
    .handle-box{
        margin-bottom: 20px;
    }
    .songList-img{
        width: 100%;
        height: 80px;
        border-radius: 5px;
        margin-bottom: 5px;
        overflow: hidden;
    }
    .handle-input{
        width: 300px;
        display: inline-block;
        margin-left:100px;
    }
    .pagination{
        display: flex;
        justify-content: center;
    }

    >>> .el-input__inner{
  width: 250px;
height:40px;
  display: inline-block;

}
.button1{
  width:100px;
  height:40px;
   background-color: #253041;
    border:0px;
  font-weight:bold;
  font-size:200;
}
.table{
    margin-top:70px;
}

.topb{
  height:50px;
  width:95%;
  padding-left: 5%;
  padding-top: 18px;
  background-color: #ffffff;
  font-size: small;
}
</style>



