<template>
    <div class="my-music">
        <div class="album-slide">
            <div class="album-img">
                <img :src="attachImageUrl(avator)">
            </div>
            <ul class="album-info">
                <li>:{{username}}</li>
                <li>:{{userSex}}</li>
                <li>{{birth}}</li>
                <li>{{location}}</li>
            </ul>
        </div>
        <div class="album-content">
            <div class="album-title">
                {{introduction}}
            </div>
            <div class="songs-body">
                <album-content :songList="collectList">
                    <template slot="title"></template>
                </album-content>
            </div>
        </div>

          <el-button type="primary" size="mini" @click="centerDialogVisible2 = true" class="button1"></el-button>
        <el-button type="primary" size="mini" @click="gocreate" class="button1"></el-button>
       <el-dialog title="" :visible.sync="centerDialogVisible2" width="400px" center>
            <el-form :model="registerForm" ref="registerForm" label-width="80px" action="" id="hh">



                <div>
                    <label></label>
                    <input type="file" name="file" multiple="multiple">
                </div>
            </el-form>
            <span slot="footer">
                <el-button size="mini" @click="centerDialogVisible2 = false"></el-button>
                <el-button size="mini" @click="addmSong"></el-button>
            </span>
        </el-dialog>
    </div>


</template>s

<script>
import {mixin} from '../mixins';
import {mapGetters} from 'vuex';
import {getUserOfId,getCollectOfUserId,songOfSongId} from '../api/index';
import AlbumContent from "../components/AlbumContent";

export default {
    name: 'my-music',
    mixins: [mixin],
    components:{
        AlbumContent
    },
    change(){
	this.$forceUpdate();  //
},
    data(){
        return {

            avator: '',       //
            username: '',     //
            userSex: '',      //
            birth: '',        //
            location: '',     //
            introduction: '', //
            collection: [],     //
            collectList: [],
              singerId: '',               //id
            singerName: '',             //

             centerDialogVisible2: false,//

            registerForm:{      //
                name: '',
                singerName: '',
                introduction: '',
                lyric: ''
            },
             form:{      //
                id: '',
                name: '',
                introduction: '',
                lyric: ''
            },
        }
    },
    computed:{
        ...mapGetters([
            'listOfSongs',      //
            'userId',           //id
        ])
    },
    mounted(){
        this.getMsg(this.userId);
        this.getCollection(this.userId);
    },
    methods:{
        getMsg(userId){
            getUserOfId(userId)
                .then(res =>{
                    this.avator = res.avator;
                    this.username = res.username;
                    if(res.sex==0){
                        this.userSex = '';
                    }else if (res.sex==1){
                        this.userSex = '';
                    }
                    this.birth = this.attachBirth(res.birth);
                    this.location = res.location;
                    this.introduction = res.introduction;
                })
                .catch(err => {
                    console.log(err);
                })
        },
        //
        getCollection(userId){
            getCollectOfUserId(userId)
                .then(res =>{
                        this.collection = res;
                        //id
                        for(let item of this.collection){
                            this.getSongsOfIds(item.songId);
                        }
                    })
                .catch(err => {
                    console.log(err);
                })
        },
        //id
        getSongsOfIds(id){
            songOfSongId(id)
                .then(res =>{
                        this.collectList.push(res);
                    })
                .catch(err => {
                    console.log(err);
                })
        },
        addmSong(){
            let _this = this;
            var form = new FormData(document.getElementById('hh'));
            form.append('singerId',1001);


            form.set('name',"");

            var req = new XMLHttpRequest();
            req.onreadystatechange = function(){
                //req.readyState == 4 
                //req.status == 200 
                if(req.readyState == 4 && req.status == 200){
                    let res = JSON.parse(req.response);
                    if(res.code){
                        _this.getData();
                        _this.registerForm = {};
                        _this.notify(res.msg,'success');
                    }else{
                         _this.notify('','error');
                    }
                }
            }
            req.open('post',`http://localhost:8888/song/addm2`,false);
            req.send(form);
             _this.notify('','success');
            _this.centerDialogVisible2 = false;

        },
        beforeSongUpload(file){
            var testMsg = file.name.substring(file.name.lastIndexOf('.') + 1);
            if(testMsg!='mp3'){
                this.$message({
                    message: 'mp3',
                    type: 'error'
                });
                return false;
            }
            return true;
        },
        //
        handleSongSuccess(res){
            let _this = this;
            if(res.code == 1){
                _this.getData();
                _this.$notify({
                    title: '',
                    type: 'success'
                });
            }else{
                _this.$notify({
                    title: '',
                    type: 'error'
                });
            }
        },
        //url
        uploadSongUrl(id){
            return `${this.$store.state.HOST}/song/updateSongUrl?id=${id}`
        },
        gocreate(){
            this.changeIndex('');
            this.$router.push({path: '/listvue'});
        },
         changeIndex(value){
            this.$store.commit('setActiveName',value);
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/my-music.scss';
</style>



