<template>
    <div class="table">
        <div class="topb">
            <h2></h2>
        </div>

        <div class="container">
            <div class="handle-box">
                <el-button type="primary" size="mini" @click="delAll" class="button1"></el-button>
                <el-input v-model="select_word" size="mini" placeholder="" class="handle-input"></el-input>
                <el-button type="primary" size="mini" @click="centerDialogVisible = true" class="button1"></el-button>

            </div>
        </div>
        <el-table size="mini" ref="multipleTable" border style="width:100%" height="680px" :data="tableData" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="40"></el-table-column>
            <el-table-column prop="name" label="-" align="center"></el-table-column>
            <el-table-column label="" width="150" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="" :visible.sync="centerDialogVisible" width="400px" center>
            <el-form :model="registerForm" ref="registerForm" label-width="80px" action="" id="tf">
                <el-form-item prop="singerName" label="" size="mini">
                    <el-input v-model="registerForm.singerName" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="songName" label="" size="mini">
                    <el-input v-model="registerForm.songName" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer">
                <el-button size="mini" @click="centerDialogVisible = false"></el-button>
                <el-button size="mini" @click="getSongId"></el-button>
            </span>
        </el-dialog>

        <el-dialog title="" :visible.sync="delVisible" width="300px" center>
            <div align="center"></div>
            <span slot="footer">
                <el-button size="mini" @click="delVisible = false"></el-button>
                <el-button size="mini" @click="deleteRow"></el-button>
            </span>
        </el-dialog>



        <el-dialog title="" :visible.sync="centerDialogVisible2" width="800px" center>

            <el-button type="primary" size="mini" @click="delAll" class="button1"></el-button>


            <el-table size="mini" ref="multipleTable" border style="width:100%" height="680px" :data="tableData" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="40"></el-table-column>
            <el-table-column prop="name" label="-" align="center"></el-table-column>

        </el-table>
            <span slot="footer">
                <el-button size="mini" @click="centerDialogVisible2 = false"></el-button>
                <el-button size="mini" @click="getSongId"></el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { mixin } from '../mixins/index';
import {listSongDetail,songOfSongId,songOfSongName,listSongAdd,delListSong} from '../api/index';

export default {
    mixins: [mixin],
    data(){
        return{
            centerDialogVisible: false,
            centerDialogVisible2: false, //
            delVisible: false,          //
            registerForm:{      //
                singerName: '',     //
                songName: ''        //
            },
            tableData: [],
            tempData: [],
            select_word: '',
            idx: -1,          //
            multipleSelection: [],   //
            songListId: ''          //id
        }
    },
    watch:{
        //table
        select_word: function(){
            if(this.select_word == ''){
                this.tableData = this.tempData;
            }else{
                this.tableData = [];
                for(let item of this.tempData){
                    if(item.name.includes(this.select_word)){
                        this.tableData.push(item);
                    }
                }
            }
        }
    },
    created(){
        this.songListId = this.$route.query.id;
        this.getData();
    },
    methods:{
        //
        getData(){
            this.tempData = [];
            this.tableData = [];
            listSongDetail(this.songListId).then(res => {
                for(let item of res){
                    this.getSong(item.songId);
                }
            })
        },
        //idtempDatatableData
        getSong(id){
            songOfSongId(id)
            .then(res => {
                this.tempData.push(res);
                this.tableData.push(res);
            })
            .catch(err => {
                console.log(err);
            });
        },
        //id
        getSongId(){
            let _this = this;
            var songOfName = _this.registerForm.singerName+"-"+_this.registerForm.songName;
            songOfSongName(songOfName).then(
                res => {
                    _this.addSong(res[0].id)
                }
            )
        },
        //
        addSong(songId){
            let _this = this;
            let params = new URLSearchParams();
            params.append('songId',songId);
            params.append('songListId',this.songListId);

            listSongAdd(params)
            .then(res => {
                if(res.code == 1){
                    this.getData();
                    this.notify("","success");
                }else{
                    this.notify("","error");
                }
            })
            .catch(err => {
                console.log(err);
            });
            _this.centerDialogVisible = false;
        },
        //
        deleteRow(){
            delListSong(this.idx,this.songListId)
            .then(res => {
                if(res){
                    this.getData();
                    this.notify("","success");
                }else{
                    this.notify("","error");
                }
            })
            .catch(err => {
                console.log(err);
            });
            this.delVisible = false;
        }
    }
}
</script>

<style scoped>
    .handle-box{
        margin-bottom: 20px;
    }
    .song-img{
        width: 100%;
        height: 80px;
        border-radius: 5px;
        margin-bottom: 5px;
        overflow: hidden;
    }
    .handle-input{
        width: 300px;
        display: inline-block;
    }
    .pagination{
        display: flex;
        justify-content: center;
    }
    .play {
        position: absolute;
        z-index: 100;
        width: 80px;
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        top: 18px;
        left: 15px;
    }

    .icon {
        width: 2em;
        height: 2em;
        color: white;
        fill: currentColor;
        overflow: hidden;
    }

    .button1{
  width:100px;
  height:40px;
   background-color: #253041;
    border:0px;
  font-weight:bold;
  font-size:200;
}
.table{
    margin-top:70px;
}
.topb{
  height:50px;
  width:95%;
  padding-left: 5%;
  padding-top: 18px;
  background-color: #ffffff;
  font-size: small;
}
</style>



